











































import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {IPayment, IStudentProfile, ITrainingSession, TrainingSessionDuration} from "@chaosinsight/yogipeople-portalclient"
import Countries from "@/assets/data/countries"
import NotificationMessage from "@/components/NotificationMessage.vue"
import PageBackground from "@/components/PageBackground.vue"
import { IPaymentMethod } from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"
import RedeemVoucher from "@/components/RedeemVoucher.vue"

@Component({components: { NotificationMessage, PageBackground, RedeemVoucher} })
export default class AddCard extends Vue {
	public sessionData!: ITrainingSession
	@Prop()
	private profile!: IStudentProfile
	private Countries = Countries
	private errMessage: string | null = null
	private duration!: TrainingSessionDuration
	private address1: string | null = null
	private address2: string | null = null
	private city: string | null = null
	private zip: string | null = null
	private country: string = ""
	private clientSecret: string = ""
	private stripe = (window as any).Stripe(process.env.VUE_APP_STRIPE_SECRET_KEY)
	private secret: string = ""
	private elements = this.stripe.elements()
	private card: any | null = null
	private addCard: boolean | null = null
	private temp: string | null = null
	private infoPlan: string = ""
	private savingCard: boolean | null = null
	private disableAdd: boolean | null = null
	private receivedVouchers: number | null = null
	private notifyMsg: string | null = null
	private firstName: string = ""
	private campaign: string | null = null
	private isSubscribed: string | null = null
	private coupon: string | null = null
	private fromLogin: string | null = null
	private checkSubscription: string | null = null
	private savedCard: IPaymentMethod[] | null = null
	private cardOptions = {
		style: {
			base: {
				"color": "white",
				"fontSize": "16px",
				"fontWeight": "300",
				"::placeholder": {
					color: "#cecece",
				},
			},
			invalid: {
				color: "#dbdbde",
			}
		},
		hidePostalCode: true
	}
	private created(): void {
		this.addCard = false
		this.getPaymentIntent()
		this.campaign = window.localStorage.getItem("camp")
		window.localStorage.removeItem("camp")
		}
	private getPaymentIntent(): void {
		Portal.client.call.payments.paymentMethodsPost()
		.response
		.then(res => {
			this.secret = res.Secret
		})
		.catch(reason => {})
	}
	private goBack(): void {
		this.$router.push("/home")
	}
	private get isFormValid(): boolean {
		return !!this.firstName && !!this.card
		}

	private mounted(): void {
		this.card = this.elements.create("card", this.cardOptions)
		this.card.mount(this.$refs.card as HTMLElement)
	}
	/**
	 * Create new payment method
	 */
	private handleCardSetup(): void {
		// if (this.voucher !== "OneMonthFree")
		// 	Portal.client.call.sessionTickets.redeemVoucher(this.voucher)
		// 	.response
		// 	.then(res => {
		// 		this.voucher = ""
		// 		this.receivedVouchers = res.length
		// 		this.notifyMsg = `You have received ${this.receivedVouchers} new session tickets`
		// 	})
		// 	.catch(err => this.notifyMsg = err)
		// 	else
			this.stripe.handleCardSetup(this.secret, this.card, {
				payment_method_data: {
				billing_details: {name: this.firstName}
				}
				}).then((result: any) => {
					if (this.coupon !== null)
						Portal.client.call.payments.applyStripeCoupon(this.coupon)
						.response
						.then(res => {
							this.coupon = ""
							this.notifyMsg = `Coupon Code applied successfully`
							this.onSucess()
							})
						.catch(err =>
							this.notifyMsg = err
							)
					else
						this.onSucess()
					})
	}
	private onSucess(): void {
					this.savingCard = true
					this.temp = window.localStorage.getItem("plan")
					window.localStorage.removeItem("plan")
					window.localStorage.setItem("isSubscribed", "yes")
					this.fromLogin = window.localStorage.getItem("routeplaninfo")
					this.checkSubscription = window.localStorage.getItem("selecttime")
					window.localStorage.removeItem("selecttime")
					if (this.checkSubscription === "yes")
						window.setTimeout(() =>
						Portal.client.call.subscriptions.get()
						.response
						.then(res => {
							if (res === undefined || res === null)
								this.$router.push("/plans")
						}), 2500)
					if (this.fromLogin === "yes")
						window.setTimeout(() => this.$router.push("/plans"), 2000 )
					else {
						if (this.temp === "basic")
							this.infoPlan = "subscription-a"
						else if (this.temp === "standard")
							this.infoPlan = "subscription-b"
						else if (this.temp === "standardcamp")
							this.infoPlan = "subscription-b"
						else if (this.temp === "pro") this.infoPlan = "subscription-c"
						else this.infoPlan = ""
						setTimeout(() => Portal.client.call.payments.paymentMethodsGet("card")
						.response
						.then(res => {
							this.savedCard = [].slice.call(res)
							if (this.savedCard!.length !== 0)
								if (this.isSubscribed === "yes")
									this.$router.push("/welcome")
								else this.$router.push("/plans")
							else this.$router.push("/plans")
					})
					.catch(reason => {}), 2000)
						setTimeout(() => {
							Portal.client.call.subscriptions.post(this.infoPlan)
							.response
							.then(res => {
								})
							} , 5000)
						window.localStorage.setItem("route", "cardadd")
							}
	}
	// create billing details for user
	private billingDetails(): object {
		return {
			address: {
				city: this.city,
				country: this.country,
				line1: this.address1,
				line2: this.address2,
				postal_code: this.zip
			},
			email: `${this.profile.Email}`,
			name: `${this.profile.FirstName} ${this.profile.LastName}`
		}
	}
}
